import React,{Component} from 'react';
import ReactDOM from 'react-dom';
import '../css/login.css';
import userdata from '../data/User_data';
import Carousel from '../js/index_carousel';
import Indexs from '../js/indexs';

class Login extends Component{
    
    click=()=>{
        var user=document.getElementById("Logins_username").value;
        var pwd=document.getElementById("Logins_password").value;
        var peopo={...userdata};
        userdata.find(e=>{
            if(e.username===user&&e.pwd===pwd){
                ReactDOM.render(<Indexs />, document.getElementById('article'));
                document.getElementById('header').removeAttribute('style');
            }
        });
    }
    register=()=>{
        // hiahiahia 
        // Alextututu 123456
        alert("");
    }
    
    render(){
        return(
            <div id="Logins_box">
                <input id="Logins_username" className="Logins_username" type="text" placeholder="/" />
                <input id="Logins_password" className="Logins_password" type="password" placeholder="" />
                <button className="Logins_logins" onClick={this.click}></button>
                <button className="Logins_registers" onClick={this.register}></button>
            </div>
        )
    }
}
export default Login;


