import React,{Component} from 'react';
import '../css/my.css';
import mydata from '../data/my_data';
import Footer from '../js/footer';

class My extends Component{
    render(){
        return(
            <div className="my_box">
                {/*  */}
                <div className="my_means">
                    {/*  */}
                    <div className="my_head"><img src={require('../img/touxiang.jpg')} /></div>
                    <div className="my_datasbox">
                        <p className="my_name"></p>
                        <p className="my_phone"><span></span>17761288547</p>
                        <p className="my_email"><span></span>969289239@qq.com</p>
                    </div>
                </div>
                {/*  */}
                <div className="my_integral">
                    <div className="my_points"><p><span>0</span></p></div>
                    <div className="my_money"><p><span>0</span></p></div>
                    <div className="my_btn"><button></button></div>
                </div>
                {/*  */}
                <div className="my_share">
                    <div className="my_share_qq"><img src={require('../img/qq@3x.png')} /><p></p></div>
                    <div className="my_share_duobao"><img src={require('../img/fenxiang@3x.png')} /><p></p></div>
                    <div className="my_share_opinion"><img src={require('../img/yijian@3x.png')} /><p></p></div>
                </div>
                {/*  */}
                <div className="my_functions">
                    <ul className="my_function_ul">
                        {/* <li className="my_function_li"><img src={require('../img/shezhi@3x.png')} /><span></span><img src={require('../img/next.png')} /></li> */}
                        {mydata.map((e,i)=>{
                            return <li key={i} className="my_function_li"><img src={require('../img/'+e.icon+'')} /><span>{e.text}</span><img src={require('../img/next.png')} /></li>
                        })}
                    </ul>
                </div>
                <Footer />
            </div>
        )
    }
}
export default My;


